@import "./basic";

.MainPage{
    height:80vh;
    background-color: red;
    display:flex;
}

.MainPage div{
    flex:1;
    text-transform: uppercase;
    background-size: cover;

    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.MainPage div a:link,
.MainPage div a:visited{
    text-decoration: none;
    font-size: 100%;
    font-weight: 600;
    color:$darkColor;
    background-color: rgba(255, 255, 250, 0.507);
    border:1px solid #555;
    border-radius: 5px;

    margin:10px;
        
    display:inline-block;
    padding:2px 5px;

    transition: color 0.2s, background-color 0.2s;

}

.MainPage div a:hover,
.MainPage div a:active{
    color:white;
    background-color:$darkColor;
    border:1px solid rgba(255, 255, 255, 0.623);
}

.bigcities{
    background: url(../resources/img/newyorkcitystreet.jpg);
    background-position: center;
}

.smalltowns{
    background: url(../resources/img/smalltown.jpg);
}

.nature{
    background: url(../resources/img/nature.jpg);
    background-position: center;
}