@import "./basic";


.BigCitiesTextChild h3{
    text-align: center;
    text-transform: capitalize;
}

.BigCitiesTextChild p{
    height:280px;
    overflow: auto;
    line-height: 145%;
    padding:20px;
}
.BigCitiesTextChild p:before{
    content:"\201C";
    font-size: 300%;
}

.bookInfo1{
    padding:0 30px;
    display: flex;
    align-items: center;
}

.BigCitiesTextChild img{
    width:80px;
    height:80px;
    object-fit: cover;
    border-radius: 50%;
}  

.bookInfo2{
    text-align: right;
}

h5{
    margin:0 0 2px auto;
}

.BigCitiesTextChild a{
    font-family:$font-basic;
    font-size: 80%;
    color:black;
    border:1px solid rgb(150, 150, 150) ;
    border-radius: 5px;
    padding:2px 5px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-left:110px ;
    transition:color 0.3s,background-color 0.3s;
}

.BigCitiesTextChild a:hover{
    color:white;
    background-color: black;
}

ion-icon{
    padding-right:8px;
}

