.Header{
    color:red;
    height:80px;
    display: flex;
    align-items: flex-end;
}

.Logo{
    flex:1;
    margin:auto auto 0px 10px;
}

.Logo img{
    width:300px;
}

.Header ul{
    flex:1;
    display: flex;
    justify-content:flex-end;
    list-style: none;
    
}

.Header li{
    padding:0 20px;
    text-align: center;
    text-transform: uppercase;
    font-size:90%;
}

.Header li a:link,
.Header li a:visited{
    padding:2px;
    text-decoration: none;
    color:black;
    border-bottom: 1px solid transparent;
    transition:border-bottom 0.2s;
}

.Header li a:hover,
.Header li a:active{
    border-bottom: 1px solid black;
}
