.BigCities {
    text-align:center;
}

.imageWraper{
    display: grid;
    grid-template-columns:repeat(4, 1fr);
    grid-template-rows: 350px 350px;
    background-color: black;
}

.imageWraper div{
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 100%;
    transition:opacity 0.5s;
    &:hover{
        opacity: 70%;
    }
}
   

a{
    text-decoration: none;
    color:white;
    font-size: 120%;
}

