body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Lato', 'Open-Sans',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
  box-sizing:border-box;
}

*:focus{
  outline:none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-transform: uppercase;
  letter-spacing: 1px;
  word-spacing: 4px;
}

p{
  line-height: 145%;
}